  padding: 0px;
  border-radius: 30px;
  /* padding: 5px; */
  width: 130px;
  height: 45px;
  line-height: 45px;
  font-weight: normal;
}
.appointment-tab .nav-tabs a {
  width: 130px;
  height: 45px;
  line-height: 45px;
  padding: 0px;
  font-weight: normal;
}
.header-navbar-rht .dropdown-menu.show {
  top: 20px;
}
.doctor-grid .profile-widget .doc-img {
  z-index: 0;
}
.slick-current div:focus {
  outline: none !important;
}
/* sidemenu */
.sideBarMenuContainer {
  position: fixed;
  height: 100%;
}

.userMenu {
  width: 100%;
  text-align: center;
  margin-right: 0px;
  margin-left: 0px;
  margin-top: 0px;
}

nav.sidebar :global(.navbar-brand) :global(.glyphicon) {
  margin-right: 0px;
}

/*Remove rounded coners*/
nav.sidebar.:global(navbar) {
  border-radius: 0px;
}

nav.sidebar {
  transition: margin 200ms ease-out;
}

/*small/medium side display*/
@media (min-width: 768px) {
  /*Center Brand*/
  nav.sidebar.:global(navbar).sidebar > .container :global(.navbar-brand),
  :global(.navbar) > :global(.container-fluid) :global(.navbar-brand) {
    margin-left: 0px;
  }
  /*Center Brand*/
  nav.sidebar :global(.navbar-brand),
  nav.sidebar :global(.navbar-header) {
    text-align: center;
    width: 100%;
    margin-left: 0px;
  }

  /*Center Icons*/
  nav.sidebar a {
    padding-right: 13px;
  }

  /*adds border top to first nav box */
  nav.sidebar :global(.navbar-nav) > li:first-child {
    border-top: 1px #e5e5e5 solid;
  }

  /*adds border to bottom nav boxes*/
  nav.sidebar :global(.navbar-nav) > li {
    border-bottom: 1px #e5e5e5 solid;
  }

  /* Colors/style dropdown box*/
  nav.sidebar :global(.navbar-nav) :global(.open) :global(.dropdown-menu) {
    position: static;
    float: none;
    width: auto;
    margin-top: 0;
    background-color: transparent;
    border: 0;
    box-shadow: none;
  }

  /*allows nav box to use 100% width*/
  nav.sidebar :global(.navbar-collapse),
  nav.sidebar :global(.container-fluid) {
    padding: 0 0px 0 0px;
  }

  /*colors dropdown box text */
  :global(.navbar-inverse)
    :global(.navbar-nav)
    :global(.open)
    :global(.dropdown-menu)
    > li
    > a {
    color: #777;
  }

  /*gives sidebar width/height*/
  nav.sidebar {
    width: 200px;
    height: 100%;
    margin-left: -160px;
    float: left;
    z-index: 8000;
    margin-bottom: 0px;
  }

  /*give sidebar 100% width;*/
  nav.sidebar li {
    width: 100%;
  }

  /* Move nav to full on mouse over*/
  nav.sidebar:hover {
    margin-left: 0px;
  }
}

@media (max-width: 768px) {
  .specialities-slider .slick-list {
    height: 120px;
  }
  .speicality-img {
    height: 65px;
    box-shadow: inherit;
  }
}

@media (max-width: 993px) {
  .specialities-slider .slick-list {
    height: 120px;
  }
  .speicality-img {
    position: relative;
    height: 65px;
    box-shadow: inherit;
  }
}

/* ck-editor content height */
.ck-editor__editable_inline {
  min-height: 200px;
}

